@mixin slide-2-shadow ($width, $bottom, $zindex) {
  background: $color-white-shadow;
  width: $width;
  height: 100px;
  position: absolute;
  bottom: $bottom;
  left: 50%;
  transform: translateX(-50%);
  z-index: $zindex;
  border-radius: 5px;
  box-shadow: 0 10px 10px 0 $color-black-shadow;
}

@mixin slide-header () {
  font: 42px "OpenSans-Light", sans-serif;
  padding-bottom: 30px;
}

@mixin centerHorizontalAbsolute () {
  left: 50%;
  transform: translate(-50%, 0);
}

@mixin boxShadowImg() {
  box-shadow: 0 10px 10px 0 $color-black-shadow;
}