@import "fonts";
@import "variables";
@import "mixings";

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
  font: 18px 'OpenSans-Regular', sans-serif;
  color: $color-white-main;
}

img {
  max-width: 100%;
}

.slide-disabled {
  display: none !important;
}

.b-slider {
  position: relative;
  width: 850px;
  height: 500px;
  border-radius: 5px;
  box-shadow: 0 10px 10px 0 lighten($color-gray-main, 20%);
  overflow: hidden;
  background: $color-gray-light; /* Old browsers */
  background: -moz-linear-gradient(top, $color-gray-light 0%, $color-gray-dark 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, $color-gray-light 0%, $color-gray-dark 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, $color-gray-light 0%, $color-gray-dark 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a9abb0', endColorstr='#6d6e7b',GradientType=0 ); /* IE6-9 */
  &.b-slider--white {
    //background: $color-white-main;
    color: $color-black-main;
    .buttons__count-slides {
      color: $color-gray-dark;
    }
    .buttons__next-btn {
      color: $color-blue-main;
      &::after {
        background-image: url('data:image/png;base64,');
      }
    }
  }

  .slides {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .slide,
  .slide__wrap {
    height: 100%;
    width: 100%;
  }

  .slide__wrap {
    position: relative;
  }

  .slide__images {
    width: 90%;
    margin: 0 auto;
  }

  .slide__text-block {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 80px;
    padding: 0 5%;
    box-sizing: border-box;
    text-align: center;
  }

  .slide__text-header {
    font: 42px "OpenSans-Light", sans-serif;
    padding-bottom: 30px;
  }
}

.buttons {
  z-index: 99;
  .buttons__close-btn {
    position: absolute;
    top: 15px;
    left: 15px;
    display: block;
    width: 15px;
    height: 15px;
    background: url('data:image/png;base64,') no-repeat center center;
    border: 0;
    background-size: 15px 15px;
  }
  .buttons__count-slides {
    position: absolute;
    bottom: 15px;
    color: $color-blue-light;
    left: 0;
    right: 0;
    text-align: center;
  }
  .buttons__next-btn {
    position: absolute;
    bottom: 15px;
    right: 40px;
    color: $color-blue-light;
    &::after {
      content: '';
      display: inline-block;
      width: 9px;
      height: 14px;
      margin-left: 10px;
      background: url('data:image/png;base64,') no-repeat center center;
      background-size: auto 100%;
    }
    &:hover,
    &:focus {
      opacity: .7;
      cursor: pointer;
    }
    &.is-disabled {
      opacity: 0.5;
      pointer-events: none;
    }
  }
}

.slide-1 {
  z-index: 10;

  .slide__wrap {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .slide-1__eggs {
    position: absolute;
    top: -60px;
    left: -150px;
    transform: scale(0.6);
    width: 746px;
    height: 591px;
    background: url("./../img/eggs.png") no-repeat;
    background-position-x: 0;
  }

  .slide-1__right-block {
    display: inline-block;
    padding-right: 60px;
  }
  .slide-1__logo {
    width: 240px;
  }

  .slide-1__right-text {
    padding-top: 35px;
    width: 320px;
    line-height: 1.5;
  }
}

.slide-2 {

  .slide__images {
    margin-bottom: 50px;
    position: relative;
  }

  .slide__image-item {
    position: relative;
    display: block;
    max-width: 100%;
    border-radius: 0 0 5px 5px;
    overflow: hidden;
    z-index: 10;
    @include boxShadowImg;
  }

  .slide-2__shadow-1 { @include slide-2-shadow(95%, 0, 6); }
  .slide-2__shadow-2 { @include slide-2-shadow(90%, 0, 5); }
  .slide-2__shadow-3 { @include slide-2-shadow(85%, 0, 4); }
  .slide-2__shadow-4 { @include slide-2-shadow(80%, 0, 3); }
  .slide-2__shadow-5 { @include slide-2-shadow(75%, 0, 2); }
  .slide-2__shadow-6 { @include slide-2-shadow(70%, 0, 1); }

}

.slide-3 {

  .slide__image-item {
    width: 200px;
    border-radius: 5px;
    overflow: hidden;
    position: absolute;
    top: 80px;
    @include boxShadowImg;
    left: 50%;
    //margin-left: -100px;

    &:nth-child(1) {
      //left: 60px;
      margin-left: 0;
      //transform: rotate(-15deg);
    }
    &:nth-child(2) {
      //left: 50%;
      margin-left: -100px;
      //transform: rotate(15deg);
    }
    //&:nth-child(3) {
    //  right: 60px;
    //  transform: rotate(-15deg);
    //}
  }

}

.slide-4 {
  background: $color-white-main;

  .slide-4__white-circle {
    position: absolute;
    top: -20%;
    bottom: -20%;
    left: -20%;
    width: 50%;
    border-radius: 50% 0 0 50%;
    //height: 100%;
    //background: red;
    background: $color-white-main;
    z-index: -1;
  }

  .slide__images {
    padding-top: 50px;
    text-align: center;
  }

  .slide__image-item {
    width: 180px;
    margin: 0 30px;
  }
}

.slide-5 {
  background: $color-white-main;
  .slide__images {
    position: relative;
    padding-top: 50px;
    text-align: center;
  }

  .slide-5__autoselect {
    position: absolute;
    top: 80px;
    margin-left: -100px;
    width: 200px;
  }
}
