  url('../fonts/OpenSans-Light/OpenSans-Light.woff') format('woff'),
  url('../fonts/OpenSans-Light/OpenSans-Light.ttf') format('truetype'),
  url('../fonts/OpenSans-Light/OpenSans-Light.svg#glyphicons_halflingsregular') format('svg');
